import { CHATSERVICE_API } from "@/config/env";
import type { ChatRoom, ChatMessage } from "@/types/chatService/chat";

const getAuthHeaders = () => {
  const token = localStorage.getItem('accessToken');
  if (!token) throw new Error("Ïù∏Ï¶ù ÌÜ†ÌÅ∞Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.");
  return { 'Authorization': `Bearer ${token}` };
};
export async function createChatRoom(participantIds: string[], roomName: string): Promise<ChatRoom> {
  const headers = getAuthHeaders();

  const response = await fetch(`${CHATSERVICE_API}/rooms`, {
    method: "POST",
    headers: {
      ...headers,
      "Content-Type": "application/json",
    },
    // üí° ÏàòÏ†ï: roomNameÏùÑ bodyÏóê Ï∂îÍ∞ÄÌï©ÎãàÎã§.
    body: JSON.stringify({ participantIds, roomName }),
  });

  if (response.ok) return await response.json();
  throw new Error("Ï±ÑÌåÖÎ∞© ÏÉùÏÑ± Ïã§Ìå®");
}

export async function getChatRooms(userSignId: string): Promise<ChatRoom[]> {
  const authHeaders = getAuthHeaders(); // getAuthHeaders()Î°ú ÌÜ†ÌÅ∞ÏùÑ Í∞ÄÏ†∏ÏòµÎãàÎã§.

  const response = await fetch(`${CHATSERVICE_API}/rooms`, {
    method: "GET",
    headers: {
      ...authHeaders,
      'Content-Type': 'application/json',
      'userSignId': userSignId,
    },
  });

  if (response.status === 204) return [];
  if (response.ok) return await response.json();
  throw new Error("Ï±ÑÌåÖÎ∞© Î™©Î°ù Ï°∞Ìöå Ïã§Ìå®");
}

export async function getChatHistory(roomId: string, page = 0, size = 50): Promise<ChatMessage[]> {
  const authHeaders = getAuthHeaders();

  const response = await fetch(`${CHATSERVICE_API}/rooms/${roomId}/messages?page=${page}&size=${size}`, {
    method: "GET",
    headers: {
      ...authHeaders,
      'Content-Type': 'application/json', // Content-TypeÏùÑ Î™ÖÏãúÌï¥Ï§çÎãàÎã§.
    },
  });

  if (response.status === 204) return [];
  if (response.ok) return await response.json();
  throw new Error("Ï±ÑÌåÖ ÌûàÏä§ÌÜ†Î¶¨ Ï°∞Ìöå Ïã§Ìå®");
}

// Î©îÏãúÏßÄ ÏùΩÏùå Ï≤òÎ¶¨
// üö® ÏàòÏ†ï: getAuthHeaders()ÏóêÏÑú Í∞ÄÏ†∏Ïò® Authorization Ìó§ÎçîÎ•º Î∞òÎìúÏãú Ìè¨Ìï®ÏãúÏºúÏïº Ìï©ÎãàÎã§.
export async function markMessagesAsRead(roomId: string): Promise<void> {
  const authHeaders = getAuthHeaders();

  const response = await fetch(`${CHATSERVICE_API}/rooms/${roomId}/read`, {
    method: "PUT",
    // ‚úÖ ÏàòÏ†ï: headers: authHeaders ÎåÄÏã† spread operatorÎ°ú Î≥ëÌï©Ìï©ÎãàÎã§.
    headers: {
      ...authHeaders,
      'Content-Type': 'application/json', // Content-TypeÏùÑ Î™ÖÏãúÌï¥Ï§çÎãàÎã§.
    },
  });

  if (!response.ok) throw new Error("Î©îÏãúÏßÄ ÏùΩÏùå Ï≤òÎ¶¨ Ïã§Ìå®");
}