// ChatRoom.tsx

import React, { useState, useEffect, useRef } from 'react';
import { useWebSocket } from '@/hooks/useWebSocket';
import { getChatHistory, markMessagesAsRead } from '@/api/chatService/chat';
import type { ChatMessage, ChatMessageDto } from '@/types/chatService/chat';
import { Send, ArrowLeft } from 'lucide-react'; // üí° ArrowLeft Ï∂îÍ∞Ä

interface ChatRoomProps {
  roomId: string;
  currentUserId: string;
  currentUserName: string;
  roomName: string; // ‚úÖ Props Ï†ïÏùòÎäî ÎêòÏñ¥ ÏûàÏùå
  onBack: () => void; // ‚úÖ Props Ï†ïÏùòÎäî ÎêòÏñ¥ ÏûàÏùå
}

// üí° ÏàòÏ†ï: PropsÎ•º Î™®Îëê Íµ¨Ï°∞ Î∂ÑÌï¥ Ìï†ÎãπÌï¥Ïïº Ìï©ÎãàÎã§.
export default function ChatRoom({ roomId, currentUserId, currentUserName, roomName, onBack }: ChatRoomProps) {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [inputMessage, setInputMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // WebSocket Ïó∞Í≤∞
  const { isConnected, sendMessage } = useWebSocket({
    url: 'http://localhost:8084/ws-chat',
    roomId,
    onMessage: (newMessage: ChatMessage) => {
      console.log('üì© ÏÉà Î©îÏãúÏßÄ ÏàòÏã†:', newMessage);
      setMessages((prev) => [...prev, newMessage]);

      // ÏûêÎèô Ïä§ÌÅ¨Î°§
      scrollToBottom();

      // ÏÉÅÎåÄÎ∞© Î©îÏãúÏßÄÎ©¥ ÏùΩÏùå Ï≤òÎ¶¨
      // üí° ÏàòÏ†ï: newMessage.userSingId -> newMessage.userSignId (Ïò§ÌÉÄ ÏàòÏ†ï)
      if (newMessage.userSignId !== currentUserId) {
        markMessagesAsRead(roomId).catch(console.error);
      }
    },
  });

  // Ï±ÑÌåÖ ÌûàÏä§ÌÜ†Î¶¨ Î°úÎìú
  useEffect(() => {
    loadChatHistory();
  }, [roomId]);

  const loadChatHistory = async () => {
    setLoading(true);
    try {
      const history = await getChatHistory(roomId);
      setMessages(history.reverse()); // Ïò§ÎûòÎêú ÏàúÏúºÎ°ú Ï†ïÎ†¨
      scrollToBottom();

      // ÏùΩÏùå Ï≤òÎ¶¨
      await markMessagesAsRead(roomId);
    } catch (err) {
      console.error('ÌûàÏä§ÌÜ†Î¶¨ Î°úÎìú Ïã§Ìå®:', err);
    } finally {
      setLoading(false);
    }
  };

  const scrollToBottom = () => {
    setTimeout(() => {
      messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    }, 100);
  };

  // Î©îÏãúÏßÄ Ï†ÑÏÜ°
  const handleSendMessage = () => {
    if (!inputMessage.trim() || !isConnected) return;

    const messageDto: ChatMessageDto = {
      // üí° ÏàòÏ†ï: userSingId -> userSignId (Ïò§ÌÉÄ ÏàòÏ†ï)
      userSignId: currentUserId,
      name: currentUserName,
      roomId,
      content: inputMessage.trim(),
      type: 'TALK',
    };

    sendMessage(messageDto);
    setInputMessage('');
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  // ÏãúÍ∞Ñ Ìè¨Îß∑
  const formatTime = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' });
  };

  return (
      <div className="flex flex-col h-full bg-white">
        {/* Ìó§Îçî */}
        <div className="flex items-center justify-between p-4 border-b">
          <div className="flex items-center">
            {/* üí° onBack Î≤ÑÌäº Ï∂îÍ∞Ä (Î™®Î∞îÏùº ÎåÄÏùë) */}
            <button onClick={onBack} className="md:hidden p-2 mr-2 hover:bg-gray-100 rounded-full transition">
              <ArrowLeft className="w-5 h-5 text-gray-600" />
            </button>
            {/* üí° roomName ÏÇ¨Ïö© */}
            <h2 className="text-lg font-bold">{roomName || 'Ï±ÑÌåÖÎ∞©'}</h2>
          </div>
          <div className="flex items-center space-x-2">
            <div className={`w-2 h-2 rounded-full ${isConnected ? 'bg-green-500' : 'bg-red-500'}`} />
            <span className="text-sm text-gray-500">
            {isConnected ? 'Ïó∞Í≤∞Îê®' : 'Ïó∞Í≤∞ Ï§ë...'}
          </span>
          </div>
        </div>

        {/* Î©îÏãúÏßÄ Î™©Î°ù */}
        <div className="flex-1 overflow-y-auto p-4 space-y-4">
          {loading ? (
              <div className="text-center text-gray-500">Î°úÎî© Ï§ë...</div>
          ) : messages.length === 0 ? (
              <div className="text-center text-gray-500">Î©îÏãúÏßÄÍ∞Ä ÏóÜÏäµÎãàÎã§.</div>
          ) : (
              messages.map((message) => {
                // üí° ÏàòÏ†ï: message.userSingId -> message.userSignId (Ïò§ÌÉÄ ÏàòÏ†ï)
                const isMyMessage = message.userSignId === currentUserId;

                return (
                    <div
                        key={message.chatId}
                        className={`flex ${isMyMessage ? 'justify-end' : 'justify-start'}`}
                    >
                      <div className={`max-w-[70%] ${isMyMessage ? 'items-end' : 'items-start'}`}>
                        {!isMyMessage && (
                            <div className="text-xs text-gray-500 mb-1">{message.name}</div>
                        )}
                        <div
                            className={`px-4 py-2 rounded-lg ${
                                isMyMessage
                                    ? 'bg-blue-500 text-white'
                                    : 'bg-gray-200 text-gray-800'
                            }`}
                        >
                          {message.content}
                        </div>
                        <div className="text-xs text-gray-400 mt-1">
                          {formatTime(message.createdAt)}
                        </div>
                      </div>
                    </div>
                );
              })
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* ÏûÖÎ†•Ï∞Ω */}
        <div className="border-t p-4">
          <div className="flex space-x-2">
            <input
                type="text"
                value={inputMessage}
                onChange={(e) => setInputMessage(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="Î©îÏãúÏßÄÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
                disabled={!isConnected}
                className="flex-1 px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-100"
            />
            <button
                onClick={handleSendMessage}
                disabled={!isConnected || !inputMessage.trim()}
                className="px-6 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:bg-gray-300 disabled:cursor-not-allowed transition"
            >
              <Send className="w-5 h-5" />
            </button>
          </div>
        </div>
      </div>
  );
}