import React, { useState, useEffect } from 'react';
import { getChatRooms } from '@/api/chatService/chat';
import type { ChatRoom } from '@/types/chatService/chat';
import { Search, Plus } from 'lucide-react';

interface ChatRoomListProps {
  currentUserId: string;
  onSelectRoom: (roomId: string, roomName: string) => void;
  onCreateRoom?: () => void;
}

export default function ChatRoomList({ currentUserId, onSelectRoom, onCreateRoom }: ChatRoomListProps) {
  const [rooms, setRooms] = useState<ChatRoom[]>([]);
  const [loading, setLoading] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');

  // ‚úÖ ÏàòÏ†ï: currentUserIdÍ∞Ä ÏûàÏùÑ ÎïåÎßå API Ìò∏Ï∂ú Î∞è ÏùòÏ°¥ÏÑ± Î∞∞Ïó¥ Ï∂îÍ∞Ä
  useEffect(() => {
    if (currentUserId) {
      loadRooms();
    }
  }, [currentUserId]);

  const loadRooms = async () => {
    setLoading(true);
    try {
      // ‚úÖ ÏàòÏ†ï: APIÏóê userId Ï†ÑÎã¨ (ÌïÑÏàò)
      const data = await getChatRooms(currentUserId);
      setRooms(data);
    } catch (err) {
      console.error('Ï±ÑÌåÖÎ∞© Î™©Î°ù Î°úÎìú Ïã§Ìå®:', err);
    } finally {
      setLoading(false);
    }
  };

  const formatTime = (dateString?: string) => {
    if (!dateString) return '';
    const date = new Date(dateString);
    const now = new Date();
    const isToday = date.toDateString() === now.toDateString();

    if (isToday) {
      return date.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' });
    }
    return `${date.getMonth() + 1}Ïõî ${date.getDate()}Ïùº`;
  };

  const filteredRooms = rooms.filter(room =>
      room.roomName?.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
      <div className="flex flex-col h-full bg-white">
        {/* Ìó§Îçî */}
        <div className="px-5 py-4 flex items-center justify-between">
          <h1 className="text-2xl font-bold text-gray-900">Î©îÏãúÏßÄ</h1>
          {onCreateRoom && (
              <button
                  onClick={onCreateRoom}
                  className="group flex items-center justify-center w-10 h-10 bg-blue-50 text-blue-600 rounded-full hover:bg-blue-600 hover:text-white transition-all duration-300"
                  title="ÏÉà Ï±ÑÌåÖ ÏãúÏûë"
              >
                <Plus className="w-5 h-5 transform group-hover:rotate-90 transition-transform duration-300" />
              </button>
          )}
        </div>

        {/* Í≤ÄÏÉâÎ∞î */}
        <div className="px-5 pb-4">
          <div className="relative">
            <Search className="absolute left-4 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
            <input
                type="text"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                placeholder="ÎåÄÌôîÎ∞© Í≤ÄÏÉâ"
                className="w-full pl-11 pr-4 py-2.5 bg-gray-100 rounded-2xl text-sm border-transparent focus:bg-white focus:border-blue-500 focus:ring-4 focus:ring-blue-50 transition-all outline-none"
            />
          </div>
        </div>

        {/* Ï±ÑÌåÖÎ∞© Î™©Î°ù */}
        <div className="flex-1 overflow-y-auto custom-scrollbar">
          {loading ? (
              <div className="p-8 text-center text-gray-500">
                <div className="animate-pulse flex space-x-4">
                  <div className="rounded-full bg-gray-200 h-12 w-12"></div>
                  <div className="flex-1 space-y-4 py-1">
                    <div className="h-4 bg-gray-200 rounded w-3/4"></div>
                    <div className="h-4 bg-gray-200 rounded"></div>
                  </div>
                </div>
              </div>
          ) : filteredRooms.length === 0 ? (
              <div className="flex flex-col items-center justify-center h-64 text-center px-4">
                <div className="w-16 h-16 bg-gray-50 rounded-full flex items-center justify-center mb-4">
                  <Search className="w-8 h-8 text-gray-300" />
                </div>
                <p className="text-gray-900 font-medium mb-1">
                  {searchQuery ? 'Í≤ÄÏÉâ Í≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§.' : 'ÎåÄÌôîÎ∞©Ïù¥ ÏóÜÏäµÎãàÎã§.'}
                </p>
                <p className="text-gray-500 text-sm">
                  {searchQuery ? 'Îã§Î•∏ Í≤ÄÏÉâÏñ¥Î°ú ÏãúÎèÑÌï¥Î≥¥ÏÑ∏Ïöî.' : 'ÏÉàÎ°úÏö¥ ÎåÄÌôîÎ•º ÏãúÏûëÌï¥Î≥¥ÏÑ∏Ïöî.'}
                </p>
              </div>
          ) : (
              <div className="divide-y divide-gray-50">
                {filteredRooms.map((room) => (
                    <div
                        key={room.roomId}
                        onClick={() => onSelectRoom(room.roomId, room.roomName || 'Ïù¥Î¶Ñ ÏóÜÎäî Î∞©')}
                        className="group flex items-center px-5 py-4 cursor-pointer hover:bg-gray-50 transition-colors"
                    >
                      {/* ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ */}
                      <div className="relative mr-4 flex-shrink-0">
                        <div className="w-12 h-12 rounded-full bg-gradient-to-br from-indigo-500 to-purple-500 flex items-center justify-center shadow-sm group-hover:shadow-md transition-shadow">
                        <span className="text-white font-bold text-lg">
                          {room.roomName ? room.roomName.charAt(0) : '?'}
                        </span>
                        </div>
                      </div>

                      {/* Ï±ÑÌåÖÎ∞© Ï†ïÎ≥¥ */}
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center justify-between mb-1">
                          <h3 className="font-semibold text-gray-900 truncate group-hover:text-blue-600 transition-colors">
                            {room.roomName || 'Ïù¥Î¶Ñ ÏóÜÎäî Î∞©'}
                          </h3>
                          <span className="text-xs text-gray-400 ml-2 flex-shrink-0">
                          {formatTime(room.lastMessageTime)}
                        </span>
                        </div>
                        <p className="text-sm text-gray-500 truncate group-hover:text-gray-700">
                          {room.lastMessageContent || 'ÏÉàÎ°úÏö¥ ÎåÄÌôîÍ∞Ä ÏãúÏûëÎêòÏóàÏäµÎãàÎã§.'}
                        </p>
                      </div>
                    </div>
                ))}
              </div>
          )}
        </div>
      </div>
  );
}